.menu {
    display: flex;
    justify-content: space-between;
    align-items: center;

    margin-bottom: 50px;

    @media screen and (min-width: 768px) {
        margin-bottom: 34px;
    }
}

.menu__logo-container {
    display: flex;
    align-items: center;
}

.menu__icon {
    width: 24px;
    height: 37px;
    padding-top: 7px;
    padding-bottom: 6px;
}

.menu__text {
    margin-left: 10px;
    font-size: 30px;
    line-height: 1.17;

    color: var(--primary-white);

    @media screen and (max-width: 767px) {
        display: none;
    }
}

.controls{
    display: flex;
    align-items: center;
}

.controls__item+.controls__item {
    margin-left: 40px;
}

.controls__link {
    line-height: 1.17;
    text-transform: uppercase;
    color: var(--primary-white);

    position: relative;
    
    transition: color 250ms cubic-bezier(0.4, 0, 0.2, 1);

    // &:hover,
    // &:focus {
    //         color: var(--accent-color);
    //     }
}

.controls__link--active {
    // color: var(--accent-color);
    pointer-events: none;

    &::after {
            content: '';
            width: 100%;
            height: 3px;
            background-color: var(--accent-color);
    
            display: block;
    
            position: absolute;
        }
}
